  public estados = Object.keys(this.data);
  public municipios:any;
  public form: FormGroup;

  constructor(private sMunicipios: SMunicipiosService,
              private formB: FormBuilder,
              private sClientes: SClientesService,
              private router: Router) {
    this.buildForm();
   }

  ngOnInit() {
  }

  public reset(){
    this.form.reset();
  }

  private buildForm(){
    this.form = this.formB.group({
      curp: ['', [Validators.required]],
      rfc: ['', [Validators.required]],
      nss: ['', [Validators.required]],
      nombre: ['', [Validators.required]],
      apaterno: ['', [Validators.required]],
      amaterno: ['', []],
      email: ['', Validators.email],
      calle: ['', [Validators.required]],
      n_ext: ['', [Validators.required]],
      n_int: [''],
      colonia: ['', [Validators.required]],
      estado: ['', [Validators.required]],
      municipio: ['', [Validators.required]],
      cp: ['', [Validators.required]],
      cr: ['', [Validators.required]],
      comentarios: ['', [Validators.required]]
    })
  }

  public getMunicipios(){
    const edo = this.form.get('estado').value;
    this.municipios = this.data[edo];
  }

  public saveForm(event: Event) {
    event.preventDefault();
    if (this.form.valid) {
      console.warn('el formulario es valido');
      this.sClientes.saveCliente(this.form.value)
        .subscribe(
          (res: any) => {
            Swal.fire({
              icon: 'success',
              title: 'Se ha guardado el cliente satisfactoriamente.',
              showConfirmButton: false,
              timer: 1500
            }).finally(
              ()=> {
                this.router.navigateByUrl('listaClientes');
              }
            )
            
          },
          err => console.error(err)
          
        )
      
    }else{
      console.error('el formulario no es valido');
      
    }
    console.log(this.form.value);
    
  }

